}
.social>a>img:hover{
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.but{
  display: flex;
  flex-direction: row;
    align-items: center;
    
}
.number{
  font-size: 26px;
  text-decoration: none;
  color: white;
}
.number:hover{
  text-decoration: underline;
  cursor: pointer;
}
.but_tel {
  width: 220px;
  text-decoration: none;
  outline: none;
  display: inline-block;
  padding: 20px 30px;
  margin: 10px 20px;
  position: relative;
  color: red;
  border: 1px solid red;
  background:  none;
  font-weight: 300;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 24px;
  cursor: pointer;
  }
  .but_tel:before,
  .but_tel:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
    box-sizing: border-box;
  }
  .but_tel:before {
    bottom: 0;
    left: 0;
    border-left: 1px solid red;
    border-top: 1px solid red;
    transition: 0s ease opacity .8s, .2s ease width .4s, .2s ease height .6s;
  }
  .but_tel:after {
    top: 0;
    right: 0;
    border-right: 1px solid red;
    border-bottom: 1px solid red;
    transition: 0s ease opacity .4s, .2s ease width , .2s ease height .2s;
  }
  .but_tel:hover:before,
  .but_tel:hover:after{
    height: 100%;
    width: 100%;
    opacity: 1;
  }
  .but_tel:hover:before {transition: 0s ease opacity 0s, .2s ease height, .2s ease width .2s;}
  .but_tel:hover:after {transition: 0s ease opacity .4s, .2s ease height .4s , .2s ease width .6s;}
  .but_tel:hover {background: rgba(255,255,255,.2);}

