  }
  .change_products:before,
  .change_products:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
    box-sizing: border-box;
  }
  .change_products:before {
    bottom: 0;
    left: 0;
    border-left: 1px solid red;
    border-top: 1px solid red;
    transition: 0s ease opacity .8s, .2s ease width .4s, .2s ease height .6s;
  }
  .change_products:after {
    top: 0;
    right: 0;
    border-right: 1px solid red;
    border-bottom: 1px solid red;
    transition: 0s ease opacity .4s, .2s ease width , .2s ease height .2s;
  }
  .change_products:hover:before,
  .change_products:hover:after{
    height: 100%;
    width: 100%;
    opacity: 1;
  }
  .change_products:hover:before {transition: 0s ease opacity 0s, .2s ease height, .2s ease width .2s;}
  .change_products:hover:after {transition: 0s ease opacity .4s, .2s ease height .4s , .2s ease width .6s;}
  .change_products:hover {background: none;
