  .order:hover:before {transition: 0s ease opacity 0s, .2s ease height, .2s ease width .2s;}
  .order:hover:after {transition: 0s ease opacity .4s, .2s ease height .4s , .2s ease width .6s;}
  .order:hover {background: rgba(255,255,255,.2);
  cursor: pointer;}

.line{
  width: 100%;
    border-bottom: 4px solid #e7e7e7;
}


/* Products */
.products-wrapper{
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}
.carousel-wrapper{
  display: flex;
  width: 45%;
  justify-content: center;
}

.carousel{
  display: flex;
  flex-direction: column;
  align-items: center;
}
li.slide.selected{
  background-color: white;
}
.products-wrapper>h1{
  font-family: 'Open Sans Condensed', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
}
.products {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
}
.name{
  display: flex;
  flex-direction: column;
  width: 40%;
}
.name>div{
  font-size: 32px;
  color: red;
}
.about{
  font-size: 18px !important;
  color:black !important;
  font-family: 'Roboto Mono', monospace;
  text-align: left;
  font-weight: 500;
}
/* Blocks about */

.gallery{
  width: 700px;
  height: 500px;
}

.title_order{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  font-weight: bolder;
  text-decoration: none;
  margin-bottom: 20px;
}

.name_order{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  font-weight: bolder;
  text-decoration: none;
  margin-bottom: 5px;
}

.name_order>a{
  color: red;
  text-decoration: none;
  font-size: 26px;
}

.name_order>a:hover{
  text-decoration: underline;
}

.about-wrapper {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  justify-content: space-evenly;
}
.about-contain-1 {
  display: flex;
  flex-direction: column;
  width: 30%;
  box-shadow: 0 0 10px black;
  align-items: center;
 
}
.header-about{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 20%;
  background: #e7e7e7;
  align-items: center;
  justify-content: space-around;
}
.header-about>h3{
  font-family: 'Open Sans Condensed', sans-serif;
  font-style: normal;
  font-size: 28px;
  color: red;
  text-align: center;

}
.contain-about{
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 80%;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto Condensed', sans-serif;
  
}
.row-about{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 25px;
}

.youtube{
  display: flex;
  width: 80%;
  height: 500px;
  margin: 0 auto;
  
}

